export default{
	props:{
		item:Object,
		index:Number
	},
	data() {
		return {
			isGuanZhu:this.item.isGuanZhu,
			infonum:this.item.infonum
		}
	},
	methods:{
		guanZhu(){//点击关注，隐藏，isGuanZhu 为true
			this.isGuanZhu=true;
			uni.showToast({//弹框
				title:'关注成功',
				icon:'success'
			})
		},
		caoZuo(type){//点击顶和踩的操作
			switch (type){
				case 'ding':
				if(this.infonum.index==1){//只执行一次
					return false;
				}
				this.infonum.ding++;
				if(this.infonum.index==2){//只执行一次
					this.infonum.cai--;
				}
				this.infonum.index=1;
					break;
				case 'cai':
				if(this.infonum.index==2){//只执行一次
					return false;
				}
				this.infonum.cai++;
				if(this.infonum.index==1){//只执行一次
					this.infonum.ding--;
				}
				this.infonum.index=2;
					break;
				default:
					break;
			}
		}
	}
}
